.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  text-align: center;
}

.headshot {
  border-radius: 50%;
  width: 200px;
  margin-bottom: 1rem;
}

h1 {
  margin-bottom: 0.5rem;
}

p {
  margin-bottom: 2rem;
  font-size: 1.25rem;
}

.objective {
  text-align: left;
  width: 75%;
  margin-top: 2rem;
}

.headshot img {
  width: 100%;
  margin-top: 1rem;
  border-radius: 5px;
}