body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
}

header h1 {
  margin: 0;
}

nav {
  display: flex;
  justify-content: center;
}

nav ul {
  list-style: none;
  display: flex;
}

nav a {
  color: white;
  text-decoration: none;
  padding: 10px 20px;
  margin-bottom: 0;
}

.navbar-brand:hover,
.navbar-brand:active {
  color: #fff;
}

.navbar-brand {
  color: #fff;
  font-size: xx-large;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 30px;
}

.navbar {
  width: 100%;
  padding: 0 20px;
}

@media (max-width: 992px) {
  .project-gallery {
    flex-direction: column;
  }

  .project {
    width: 100%;
    margin-bottom: 1rem;
  }
}
.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  text-align: center;
}

.headshot {
  border-radius: 50%;
  width: 200px;
  margin-bottom: 1rem;
}

h1 {
  margin-bottom: 0.5rem;
}

p {
  margin-bottom: 2rem;
  font-size: 1.25rem;
}

.objective {
  text-align: left;
  width: 75%;
  margin-top: 2rem;
}

.headshot img {
  width: 100%;
  margin-top: 1rem;
  border-radius: 5px;
}
.project {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1rem;
    width: 30%;
    text-align: center;
    background-color: rgb(204, 238, 250);
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .project-title {
    padding: 1rem;
    background-color: lightblue;
    width: 100%;
  }
  
  .proj-links {
    display: flex;
    justify-content: space-around;
    width: 100%;
  }
  
  .proj-scr {
    width: 100%;
    padding: 1rem;
  }
  
  
  .project a {
    margin-bottom: 1rem;
    color: #333;
    text-decoration: none;
  }

  .project a:hover {
    font-weight: bold;
  }
  
  .project .proj-desc {
    font-size: small;
    padding: 1rem;
    min-height: 8rem;
  }
  
  .project img {
    object-fit: contain;
    width: 100%;
    height: 300px;
 }
 
.project-gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 2rem;
  }
  
  .project-gallery .project-pg {
    width: 30%;
    margin: 1rem;
  }
.contact-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 50px 0;
}

@media only screen and (max-width: 700px) {
  .contact-container {
    flex-direction: column;
    align-items: center;
  }
}

.contact-info,
.contact-form {
  width: 45%;
  text-align: center;
}

.contact-form {
  border-left: 1px solid lightgray;
}

@media only screen and (max-width: 700px) {
  .contact-info,
  .contact-form {
    width: 90%;
    margin-bottom: 50px;
  }
  .contact-form {
    border: 0;
  }
  .contact-info {
    border-bottom: 1px solid lightgray;
  }
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

input[type="text"],
input[type="email"],
textarea {
  width: 80%;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 5px;
  border: 1px solid gray;
}

button[type="submit"] {
  padding: 10px 20px;
  /* background-color: lightblue; */
  border-radius: 5px;
  border: none;
  color: white;
  cursor: pointer;
}

